import React from 'react'
import '../styles/Layout.scss'
import {library} from '@fortawesome/fontawesome-svg-core'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faHome, faUser, faLayerGroup, faDesktop, faEnvelopeOpenText} from '@fortawesome/free-solid-svg-icons'

const Layout = props => {

  library.add(faHome, faUser, faLayerGroup, faDesktop, faEnvelopeOpenText)

  return(
    <div className='Layout'>
      <div className="navbar">
        <nav className='nav'>
          <span className='nav-label'>Home
          <FontAwesomeIcon className='nav-icon' icon='home'/></span>
          <span className='nav-label'>About
          <FontAwesomeIcon className='nav-icon' icon='user'/></span>
          <span className='nav-label'>Stack
          <FontAwesomeIcon className='nav-icon' icon='layer-group'/></span>
          <span className='nav-label'>Portfolio
          <FontAwesomeIcon className='nav-icon' icon='desktop'/></span>
          <span className='nav-label'>Contact
          <FontAwesomeIcon className='nav-icon' icon='envelope-open-text'/></span>
        </nav>
      </div>
      <div>
        {props.children}
      </div>
    </div>
  )
}

export default Layout